import React, { useEffect, useState } from 'react';
import { TabContent, TabPane, Nav, NavItem, NavLink, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import VideoCard from './VideoCard';
import requests from './requests';
import axios from './axios';
import genres from './Genres';
import './Tab.css';
import FlipMove from 'react-flip-move';

const Example = (props) => {
  // This is the setState for the tab
  const [activeTab, setActiveTab] = useState(requests.fetchTrending);

  const toggle = tab => {
    if(activeTab !== tab) setActiveTab(tab);
  }

  // This is the setState for the movies in the tabpane
  const [movies, setMovies] = useState([]);

  useEffect(() => {
    async function fetchData() {
      const request = await axios.get(activeTab)
      setMovies(request.data.results);
      return request;
    }

    fetchData();
  }, [activeTab] )

  const genreName = genres.map(genre => 
    (
    <NavItem className="genreName" >
      <NavLink
            className={classnames({ active: activeTab === genre.link })}
            onClick={() => { toggle(genre.link); }}  key={genre.id} id="navtab"
          >
            {genre.title}
        </NavLink>
      </NavItem>
    )) ;

    

  const genreItem = genres.map((genre) =>
  (<TabPane tabId={genre.link} key={genre.id} >
    <Row >
        <FlipMove className="tabcontent" >
        {movies.filter((movie) => (
            movie.genre_ids.includes(genre.id) || activeTab)).map((movie) =>
            <Col sm="10" md="6" lg="4"  >
              (<VideoCard movie={movie} />)
            </Col>
            )}
      </FlipMove>
    </Row>
  </TabPane>
  ));

  return (
    <div>
      <Nav tabs id="nav">
          {genreName}
      </Nav>
      <TabContent activeTab={activeTab} className="tabgrid"  >
            {genreItem} 
      </TabContent>
    </div>
   
  );
}

export default Example;

