import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  NavbarText
} from 'reactstrap';
import HomeIcon from '@material-ui/icons/Home';
import FlashOnIcon from '@material-ui/icons/FlashOn';
import LocalOfferIcon from '@material-ui/icons/LocalOffer';
import hulupic1 from './Images/hulupic1.png'
import './Header.css'


const Header = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar color="none" expand="md" className="navBar" >
        <NavbarBrand href="/" id="navBrand" >
             <img src={hulupic1} alt="Hulu" /> 
        </NavbarBrand>
        <NavbarToggler onClick={toggle} id="collapse" />
        <Collapse isOpen={isOpen} navbar >
          <Nav className="mr-auto" navbar id="nav" >
            <NavItem className="navItem" >
                <NavLink href="/components/" className="link">
                    <HomeIcon />
                </NavLink>
                <p>Home</p>
            </NavItem>
            <NavItem className="navItem">
                <NavLink href="#" className="link">
                    <FlashOnIcon />
                </NavLink>
                <p>Trending</p>
            </NavItem>
            <NavItem className="navItem" >
                <NavLink href="#" className="link"> 
                    <LocalOfferIcon />
                </NavLink>
                <p>Pricing</p>
            </NavItem>
          </Nav>
          <NavbarText className="navtext" >
                <p>Sign up</p>
          </NavbarText>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Header;